export default {
	props: {
		/**
		 * 输入类型
		 */
		type: {
			type: String,
			default: 'text'
		},
		/**
		 * 值
		 */
		value: String,
		/**
		 * 占位符
		 */
		placeholder: String,
		/**
		 * 是否显示清除按钮
		 */
		clearable: {
			type: [Boolean, String],
			default: false
		},
		/**
		 * 是否显示密码可见按钮
		 */
		displayable: {
			type: [Boolean, String],
			default: false
		},
		/**
		 * 自动获取焦点
		 */
		focus: {
			type: [Boolean, String],
			default: false
		}
	},
	methods: {
		onFocus() {
			this.$emit('focus')
		},
		onInput(e) {
			this.$emit('input', e.target.value)
		}
	}
}
